import type { CMSAppartmentData, CMSAppartmentItem, Rental, SearchParameters } from '$types/api';

import { buildQueryParams } from './buildQueryParams';
import { apiBaseUrl, GOOGLE_MAPS_API_BASE_URL } from './constants';

export class ApiClient {
  /**
   *
   * @param queryParams to include as part of query for the request
   * @returns offers data for a group of apartments
   */
  public async getGroupApartmentsData(queryParams: SearchParameters): Promise<Rental[] | null> {
    const url = apiBaseUrl + 'groupApartments' + buildQueryParams(queryParams);

    const response = await fetch(url);
    const body = await response.json();
    if (body.error || !body.data) return null;

    return body as Rental[];
  }

  /**
   *
   * @param queryParams to include as part of query for the request
   * @returns offers data for a single hotel
   */
  public async getSingleHotelOffersData(queryParams: SearchParameters): Promise<Rental | null> {
    const url = apiBaseUrl + 'offers' + buildQueryParams(queryParams);

    const response = await fetch(url);
    const body = await response.json();
    if (body.error || !body.data) return null;

    return body as Rental;
  }

  /**
   *
   * @param queryParams to include as part of query for the request
   * @returns CMS apartment data
   */
  public async getCMSApartments(): Promise<CMSAppartmentData | null> {
    const url = apiBaseUrl + 'allCmsApartments';

    const response = await fetch(url);
    const body = await response.json();
    if (!body.items) return null;
    // temporarily update the price to a random value
    body.items.forEach((apartment: CMSAppartmentItem) => {
      apartment.fieldData.price = Math.max(50, Math.round(Math.random() * 35) * 10);
    });

    return body as CMSAppartmentData;
  }

  public async updateApartment(apartment: CMSAppartmentItem): Promise<void> {
    const url = apiBaseUrl + 'updateApartment';

    // clone the apartment and remove the price from the fieldData
    const updatedFieldData = { ...apartment.fieldData };
    delete updatedFieldData.price;
    const updatedApartment = { ...apartment, fieldData: updatedFieldData };

    const response = await fetch(url, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(updatedApartment),
    });

    if (!response.ok) {
      console.error('Failed to update apartment');
    }
  }

  /**
   *
   * @param address The address to get the latitude and longitude from
   * @returns The latitude and longitude of the address
   */
  public static async getLatLongFromAddress(
    address: string
  ): Promise<{ lat: number; lng: number } | null> {
    try {
      const encodedAddress = encodeURIComponent(address);
      const url = GOOGLE_MAPS_API_BASE_URL + `address=${encodedAddress}`;
      const response = await fetch(url);

      const data = await response.json();

      if (data.status === 'OK') {
        const { lat, lng } = data.results[0].geometry.location;
        return { lat, lng };
      }
      throw new Error(`Geocoding failed: ${data.status}`);
    } catch (error) {
      console.error(error);
      return null;
    }
  }
}
