import type { SearchParameters } from '$types/api';

/**
 *
 * @param queryParams to build
 * @returns string representation of query params
 */
export const buildQueryParams = (queryParams: SearchParameters) => {
  let url = '?';
  for (const [key, value] of Object.entries(queryParams)) {
    if (value) {
      url += `${key}=${value}&`;
    }
  }
  return url;
};
