import Splide from '@splidejs/splide';

import type { CMSAppartmentItem, CMSImage } from '$types/api';
import type { Category } from '$types/types';

import { SELECTORS, THUMBNAIL_KEYS } from './constants';

/**
 *
 * @param scope the scope to search in
 * @param selector the selector to search for
 * @param data the data to bind
 */
export const bindData = (scope: Element, selector: string, data: string | number) => {
  const elements = scope.querySelectorAll(selector);
  elements.forEach((element) => {
    element.innerHTML = String(data);
  });
};

/**
 * Gets the thumbnails associated with a given CMS appartment Item
 * @param apartment is the CMS Apartment Itme
 * @returns list of thumbnails
 */

export const getApartmentThumbnails = (apartment: CMSAppartmentItem): CMSImage[] => {
  const thumbnails: CMSImage[] = [];
  const keys = [...THUMBNAIL_KEYS];
  for (let i = 0; i <= keys.length; i++) {
    const thumbnail = apartment.fieldData[keys[i]];
    if (thumbnail) {
      thumbnails.push(thumbnail);
    }
  }
  return thumbnails;
};

/**
 * Sets up the Splide slider for the apartment.
 */
export const setupSplider = (element: HTMLElement) => {
  const splide = new Splide(element, {
    type: 'loop',
    pagination: false,
  });

  splide.mount();
};

/**
 *  returns the locale based on the language
 * @param lang is the language currently selected
 * @returns the locale
 */
export const getLocale = (lang: string) => {
  const locales: { [key: string]: string } = {
    en: 'en-GB',
    fr: 'fr-FR',
  };
  return locales[lang.toLowerCase()] || 'fr-FR';
};

/**
 * Get all the Categories data from the hidden rendered list
 */

export const getCategories = (): { [key: string]: Category } => {
  const tagList = document.querySelector<HTMLDivElement>(SELECTORS.tagList);
  const categories: { [key: string]: Category } = {};

  if (tagList) {
    // get the categories from all the children
    for (let i = 0; i < tagList.children.length; i++) {
      const categoryEl = tagList.children[i];
      // extract the id, name, url and color
      const id = categoryEl.getAttribute('data-id');
      const name = categoryEl.querySelector(SELECTORS.name)?.textContent;
      const url = categoryEl.querySelector('img')?.getAttribute('src');
      const color = categoryEl.querySelector<HTMLDivElement>(SELECTORS.container)?.style
        .backgroundColor;
      const textColor = categoryEl.querySelector<HTMLDivElement>(SELECTORS.container)?.style.color;

      if (id && name && url && color && textColor) {
        categories[id] = {
          id,
          name,
          url,
          color,
          textColor,
        };
      }
    }
  }
  return categories;
};
