import { OverlappingMarkerSpiderfier } from 'ts-overlapping-marker-spiderfier';

import { SELECTORS } from './constants';

export class GoogleMaps {
  private mapElement: HTMLDivElement | null = null;
  private mapRendered = false;

  private markers: google.maps.Marker[] = [];
  private oms: OverlappingMarkerSpiderfier | null = null;

  constructor() {
    const mapContainer = document.querySelector<HTMLDivElement>(SELECTORS.mapContainer);

    if (mapContainer) {
      this.mapElement = document.createElement('div');
      this.mapElement.style.width = '100%';
      mapContainer.appendChild(this.mapElement);
    }
  }

  public initializeMap(
    items: { color: string; textColor: string; lng: number; lat: number; price: string }[]
  ) {
    if (!window.mapIsReady || !this.mapElement) return;
    if (this.mapRendered) {
      // remove all markers
      this.markers.forEach((marker) => {
        if (this.oms) this.oms.removeMarker(marker);
      });
    }
    this.mapRendered = true;

    // randomize some overlapping map data -- more normally we'd load some JSON data instead
    const latitude = 48.8575;
    const longitude = 2.3514;

    // update the height of the map element based on the parent's height
    const { height } = this.mapElement?.parentElement?.getBoundingClientRect() || { height: 0 };
    this.mapElement.style.height = `${height - 50}px`;

    const iconWithColours = (function () {
      function processTemplate(str: string) {
        const template = str.split('`');
        for (let i = 0, len = template.length; i < len; i += 2)
          template[i] = encodeURIComponent(template[i]);
        return template;
      }
      function applyTemplate(
        template: string[],
        values: {
          r?: number;
          g?: number;
          b?: number;
          fill?: string;
          stroke?: string;
          plus?: string;
          textColor?: string;
          price?: string;
        }
      ) {
        const result = template.slice();
        for (let i = 1, len = template.length; i < len; i += 2) {
          // result[i] = values[result[i]]
          result[i] = values[result[i]] || result[i];
        }
        return result.join('');
      }

      const svgTemplate = processTemplate(
        '<svg viewBox="0 0 23 32" xmlns="http://www.w3.org/2000/svg"><g fill="none" fill-rule="evenodd"><path d="M22 11c0 1.42-.226 2.585-.677 3.496l-7.465 15.117c-.218.43-.543.77-.974 1.016-.43.246-.892.37-1.384.37-.492 0-.954-.124-1.384-.37-.43-.248-.75-.587-.954-1.017L1.677 14.496C1.227 13.586 1 12.42 1 11c0-2.76 1.025-5.117 3.076-7.07C6.126 1.977 8.602 1 11.5 1c2.898 0 5.373.977 7.424 2.93C20.974 5.883 22 8.24 22 11z" stroke="`stroke`" stroke-width=".6" fill="`fill`" fill-rule="nonzero"/>`plus`</g></svg>'
      );

      const svgTemplate2 = processTemplate(
        '<svg xmlns="http://www.w3.org/2000/svg" width="100" height="40"> <defs><style type="text/css"> @import url("https://fonts.googleapis.com/css?family=Figtree:400,100,100italic,300,300italic,400italic,500,500italic,700,700italic,900,900italic");</style></defs> <rect width="100" height="30" fill="`fill`"></rect>  <polygon points="40 30, 60 30, 50 40" fill="`fill`" stroke="none" />`price`</svg>'
      );

      const plusTemplate = processTemplate(
        '<path d="M17 11.012c0-.607-.51-1.117-1.115-1.117h-3.222v-3.23c0-.63-.533-1.165-1.163-1.165s-1.163.534-1.163 1.166v3.23H7.115C6.51 9.895 6 10.405 6 11.01c0 .607.51 1.117 1.115 1.117h3.222v3.204c0 .632.533 1.166 1.163 1.166s1.163-.534 1.163-1.166V12.13h3.222c.606 0 1.115-.51 1.115-1.118z" fill="`fill`"/>'
      );

      const texTemplate = processTemplate(
        '<text x="50%" y="50%" font-size=".875rem" style="font-weight:550;font-family:Figtree,sans-serif;" fill="`textColor`" text-anchor="middle">`price`</text>'
      );

      return function (plus: string, price: string, color: string, textColor: string) {
        const svg = applyTemplate(plus ? svgTemplate : svgTemplate2, {
          fill: color,
          stroke: color,
          plus: plus ? applyTemplate(plusTemplate, { fill: 'white' }) : '',
          price: applyTemplate(texTemplate, { price: price, textColor }),
        });
        return 'data:image/svg+xml,' + svg;
      };
    })();

    const map = new google.maps.Map(this.mapElement, {
      center: { lat: latitude, lng: longitude },
      mapTypeControl: false,
      scaleControl: false,
      scrollwheel: false,
      streetViewControl: false,
      fullscreenControl: false,
      zoom: 13.5,
      mapId: 'fc1fadce4c1818ea',
    });

    const iw = new google.maps.InfoWindow();
    function iwClose() {
      iw.close();
    }
    google.maps.event.addListener(map, 'click', iwClose);

    this.oms = new OverlappingMarkerSpiderfier(map);

    const addMarkerWithData = (markerData: {
      lng: number;
      lat: number;
      color: string;
      price: string;
      textColor: string;
    }) => {
      const marker = new google.maps.Marker({
        position: markerData,
        draggable: false,
        optimized: false,
      });

      // temporarily update the longitude and latitude
      if (!markerData.lng || !markerData.lat) {
        markerData.lng = longitude + (Math.random() - 0.5) * 0.1;
        markerData.lat = latitude + (Math.random() - 0.5) * 0.1;
      }

      google.maps.event.addListener(marker, 'click', iwClose);
      google.maps.event.addListener(marker, 'spider_format', (status: string) => {
        marker.setIcon({
          url: iconWithColours(
            status === OverlappingMarkerSpiderfier.markerStatus.SPIDERFIABLE ? 'white' : '',
            markerData.price,
            markerData.color,
            markerData.textColor
          ),
          // scaledSize:  new google.maps.Size(23, 32), // makes SVG icons work in IE
          scaledSize:
            status === OverlappingMarkerSpiderfier.markerStatus.SPIDERFIABLE
              ? new google.maps.Size(60, 38)
              : undefined, // makes SVG icons work in IE
        });
      });
      if (this.oms)
        this.oms.addMarker(marker, function () {
          // iw.setContent(
          //   'R: ' + markerData.rgb.r + '<br />G: ' + markerData.rgb.g + '<br />B: ' + markerData.rgb.b
          // );
          // iw.open(map, marker);
        });
      this.markers.push(marker);
    };

    for (let i = 0, len = items.length; i < len; i++) addMarkerWithData(items[i]);
  }
}
